.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

body {
    display: block;
    background: #161616;
    width: 100%;
    height: 5000px;
}

:start {
    height: 300px;
    width: 150px;
}

.navBar {
    color: white;
    width: 100%;
    position: fixed;
    background: black;
    z-index: 5;
}
.navBar table {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 6px;
    min-width: 500px;
    width: 50%;
    max-width: 800px;
}
.navBar tr {
    display: flex;
    justify-content: space-between;
    padding: 0;
}
.navBar .menue {
    margin-top: 10px;
    font-size: 14px;
}
.navBar .menue a {
    cursor: pointer;
}
.navBar .details {
    margin-top: 40px;
    font-size: 24px;
}
.navBar .details #name {
    font-weight: 600;
}
.navBar .details #LinkedIn div {
    width: 150px;
    height: 30px;
    text-align: center;
    line-height: 0.9;
    font-weight: bolder;
    font-size: 34px;
    background: #0077b5;
    border-radius: 20px;
    cursor: pointer;
}

.profile {
    position: fixed;
    top: 200px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    border-radius: 400px;
    color: white;
    max-height: 45vh;
    min-height: 10px;
    z-index: 6;
}

:buffertop {
    min-height: 200px;
    height: 220px;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
}
:buffertop a {
    margin-left: auto;
    margin-right: auto;
    color: white;
}

:profile {
    background: black;
    height: 75vh;
    max-height: 800px;
    display: flex;
    position: relative;
    z-index: 1;
}
:profile :stars {
    position: absolute;
    align-items: center;
    z-index: 0;
    display: flex;
    width: 100vw;
    -webkit-mask-image: linear-gradient(to bottom, black 1%, transparent 100%);
    mask-image: linear-gradient(to bottom, black 0%, transparent 100%);
}
:profile a {
    color: white;
    font-size: 100px;
    font-weight: 500;
    align-items: center;
    align-self: flex-end;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15vh;
    display: flex;
    z-index: 1;
}

:blue {
    color: #0077b5;
    font-weight: 500;
}
